.box1{
    background: rgb(250, 248, 248);
    width: min-content;
    height: auto;
    padding: 10px;
    margin-top: 10px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0,0,0,0.2);
    box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;
    
    font-size: 25px;
    
}
a {
    display: inline-block;
    width: 150px;
    margin: 5px;   
    padding-bottom: 5px;  
    text-decoration: none;
    color: #4B71FE;
}
a:hover{
    color: rgb(145, 7, 145)
}
html{
    text-align: center;
}
.std-btn{
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
    background-color: #A0AEBF;
    width: 170px;
    height: 35px;
    font-size: 20px;
    border: 0;
    border-radius: 5px;
    outline: none;
}
h1{
    font-size: 25px;
}
